










export default {
	data() {
		return {
			text: ''
		};
	},
	onLoad: function() {
		let that = this;
		this.$api.help({}, function(res) {
			console.log(res)
			if (res.data.status == 1) {
				that.text = res.data.data;
			}
		});
	}
}



